import React, {useState} from 'react';
import { Segment, Menu, Visibility, Container, Grid } from 'semantic-ui-react';
import { HomePageHeading, Demo } from './body/index';


function DesktopApp() {
  const [isfixed, setFixedState] = useState(false);
  return <>
    <Visibility
      once={false}
      onBottomPassed={() => setFixedState(true)}
      onBottomPassedReverse={() => setFixedState(false)}
    >
      <Segment
          inverted
          textAlign='center'
          style={{ minHeight: 500, padding: '1em 0em' }}
        vertical
      >
        <Menu
          fixed={isfixed ? 'top' : undefined}
          inverted={!isfixed}
          pointing={!isfixed}
          secondary={!isfixed}
          size='large'
        >
          <Container>
            <Menu.Item as='a' active>
              Home
            </Menu.Item>
            <Menu.Item as='a'>Paper</Menu.Item>
            <Menu.Item as='a'>Demo</Menu.Item>
            <Menu.Item as='a'>Contact</Menu.Item>
          </Container>
        </Menu>
        <HomePageHeading/>
      </Segment>
    </Visibility>
    <Segment style={{ padding: '4em 0em' }} vertical>
      <Demo/>
    </Segment>
  </>;
}

export default DesktopApp;